import { IonSpinner } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React, { useState } from 'react'
import * as FIcon from 'react-feather'
import { RouteComponentProps } from 'react-router'
import { FooterFormulario, Page } from '../../components'
import { SubHeader } from '../../components/SubHeader'
import { FORMATO_FECHA_BACKEND, RUTAS_APP_FABRICANTE } from '../../constants'
import { plata_fmt } from '../../helpers'
import { useStores } from '../../models/root-store/root-store-context'
import { MovimientoTransportista } from '../../services/api'
import '../Transportista/GananciaTransportista.css'

const numeral = require('numeral')
const moment = require('moment-timezone')

export const FabricanteBalance: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const { transportistaStore } = rootStore
  const [Loading, setLoading] = useState(false)
  const [MovimientosPendienteFacturacion, setMovimientosPendienteFacturacion] = useState(
    [] as MovimientoTransportista[],
  )
  const [MovimientosFacturados, setMovimientosFacturados] = useState([] as MovimientoTransportista[])
  const [MovimientosCobrados, setMovimientosCobrados] = useState([] as MovimientoTransportista[])
  const [Balance, setBalance] = useState({
    facturado: 0,
    pendiente_facturacion: 0,
    cobrado: 0,
    cantidad: 0,
    ganancia_intervalo: 0,
  })
  const [Diario, setDiario] = useState(true)

  const [FechaSeleccionada, setFechaSeleccionada] = useState(moment().format())
  const [FechaInicio, setFechaInicio] = useState<null | string>(null)
  const [FechaFin, setFechaFin] = useState<null | string>(null)
  const Movil = window.innerWidth <= 500 || /Mobi/.test(navigator.userAgent)

  const setup = () => {
    setLoading(true)
    obtenerBalance()
  }

  React.useEffect(() => {
    if (!!FechaInicio && !!FechaFin) {
      setup()
    }
  }, [FechaInicio, FechaFin, Diario])

  React.useEffect(() => {
    if (Diario) {
      setFechaInicio(moment(FechaSeleccionada).subtract(1, 'days').format(FORMATO_FECHA_BACKEND))
      setFechaFin(moment(FechaSeleccionada).add(1, 'days').format(FORMATO_FECHA_BACKEND))
    } else {
      setFechaInicio(moment(FechaSeleccionada).startOf('month').subtract(1, 'days').format(FORMATO_FECHA_BACKEND))
      setFechaFin(moment(FechaSeleccionada).endOf('month').add(1, 'days').format(FORMATO_FECHA_BACKEND))
    }
  }, [FechaSeleccionada, Diario])

  const obtenerBalance = () => {
    rootStore.environment.api.getBalanceDetail(10, FechaFin, FechaInicio).then((result) => {
      if (result.kind === 'ok') {
        setBalance(result.data)
        transportistaStore.setBalance(result.data.pendiente_facturacio)
        setLoading(false)
      }
    })
  }

  return (
    <Page login_required fabricante={true} disableScroll={true} id={'balance-fabricante'}>
      <SubHeader noContainer links={[{ titulo: 'Balance' }]} />
      <div id={'transportista-balance-container'}>
        {Loading && <IonSpinner name="crescent" className={'loadingSpinner'} />}
        {!Loading && (
          <div className={Movil ? 'container-lg' : ''}>
            <div className="row">
              <div className="col">
                <button
                  onClick={() => setDiario(true)}
                  className={Diario ? 'btn-outline-verde-abi' : 'btn-outline-gris'}
                >
                  Día
                </button>
              </div>
              <div className="col">
                <button
                  onClick={() => setDiario(false)}
                  className={!Diario ? 'btn-outline-verde-abi' : 'btn-outline-gris'}
                >
                  Mes
                </button>
              </div>
            </div>
            <div className="fecha-seleccionada-container">
              <a
                onClick={() => {
                  const currentDate = moment(FechaSeleccionada)
                  if (Diario) {
                    setFechaSeleccionada(currentDate.subtract(1, 'days'))
                  } else {
                    setFechaSeleccionada(currentDate.subtract(1, 'months'))
                  }
                }}
              >
                <FIcon.ChevronLeft style={{ color: 'var(--verde-claro)'}} />
              </a>

              <div>
                {Diario && (
                  <span style={{ fontWeight: 'bolder' , textTransform: 'capitalize'}}>
                    {moment(FechaSeleccionada).format('dddd[,]  D [de] MMMM')}
                  </span>
                )}
                {!Diario && (
                  <span style={{ fontWeight: 'bolder', textTransform: 'capitalize' }}>
                    {moment(FechaSeleccionada).startOf('month').format('MMMM')}
                  </span>
                )}
                <br />
                <span style={{ fontWeight: 'bold', color: 'var(--verde-claro)', fontSize: 25 }}>
                  {plata_fmt(Balance.ganancia_intervalo)}
                </span>
                <br />
                <span style={{ color: 'var(--verde-claro)' }}> Ganancias</span>
              </div>
              <a
                onClick={() => {
                  const currentDate = moment(FechaSeleccionada)
                  if (Diario) {
                    setFechaSeleccionada(currentDate.add(1, 'days'))
                  } else {
                    setFechaSeleccionada(currentDate.add(1, 'months'))
                  }
                }}
              >
                <FIcon.ChevronRight style={{ color: 'var(--verde-claro)'}} />
              </a>
            </div>

            <div className="monto-total-balance-transportista-container">
              <span>Ganancia total</span>
              <br />
              <span className="monto-total-balance-transportista">
                {plata_fmt(Balance.facturado + Balance.pendiente_facturacion)}
              </span>
            </div>

            <div className="monto-total-balance-transportista-container">
              <span>Cobrado</span>
              <br />
              <span className="monto-total-balance-transportista">{plata_fmt(Balance.cobrado)}</span>
            </div>
            <div className="monto-total-balance-transportista-container">
              <span>Pendiente a cobrar</span>
              <br />
              <span className="monto-total-balance-transportista">
                {plata_fmt(Balance.facturado + Balance.pendiente_facturacion - Balance.cobrado)}
              </span>
            </div>
          </div>
        )}
      </div>
    </Page>
  )
})

export default FabricanteBalance
